<script>
	import axios from 'axios';
	import { onMount, tick } from 'svelte';
	
	const HOST = `${
		location.protocol}//${location.hostname === 'localhost' ? 'localhost:3000' : location.hostname
	}`.replace(/^http/, 'ws', 'one');

	let allMessages = [];
	let message = '';
	let sendMessage;
	let messagesRef;
	let handleEnter;
	let userScrollDisable = false;

	const socket = new WebSocket(HOST);
	socket.onerror = (e) => {
		console.log(e);
	};

	socket.onmessage = async (e) => {
		const newMessages = allMessages.slice();
		newMessages.push(e.data);
		allMessages = newMessages;
		await tick();
		messagesRef.scrollTo({ top: messagesRef.scrollHeight, behavior: 'smooth' });
	};

	sendMessage = () => {
		if (message) {
			socket.send(message);
			message = '';
		}
	};

	handleEnter = (e) => {
		if (e.key === 'Enter' && message) {
			socket.send(message);
			message = '';
		}
	};

	onMount(() => {
		axios.get('/getLastMessages').then( async (res) => {
			allMessages = res.data;
			await tick();
			messagesRef.scrollTop = messagesRef.scrollHeight;
		});
	});

</script>

<div class='main'>
	<div class='messages' bind:this={messagesRef}>
		{#each allMessages as msg}
			<div>
				<p>user</p>
				<p>{msg}</p>
			</div>
		{/each}
	</div>
	<div class='input_wrapper'>
		<input type="text" class='input' bind:value={message} on:keypress={handleEnter}>
		<div class='send_message'>
			<span on:click={sendMessage}>Send</span>
		</div>
	</div>
</div>

